/* make variables */
:root {
    --lightgray: #d9d9d9;
    --gray: #9c9c9c;
    --orange: #f48915;
    --darkGrey: #464d53;
    --caloryCard: #656565;
    --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);
    --appColor: #3c3f45;
}

.App {
    background-image: url(data:image/png;base64,);
    mix-blend-mode: overlay;
    background-color: var(--appColor);
    display: flex;
    flex-direction: column;
    gap: 6rem;
    overflow: hidden;
}
::-webkit-scrollbar {
    display: none;
}

.stroke-text {
    color: transparent;
    font-family: Arial, Helvetica, sans-serif;
    -webkit-text-stroke-color: var(--lightgray);
    -webkit-text-stroke-width: 2px;
}
button:hover,
.btn {
    cursor: pointer;
}

.blur {
    background-color: rgba(244, 137, 21, 0.5);
    position: absolute;
    width: 40%;
    height: 40%;
    filter: blur(150px);
    z-index: -9;
    border-radius: 50%;
}
